@font-face {
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-style: normal;
  font-weight: 300;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 30px;
  color: #283645;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

svg {
  display: block;
}

button {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.container {
  padding: 0 20px;
}

@media (min-width: 1200px) {
  .container {
    max-width: 980px;
    padding: 0 20px;
    margin: 0 auto;
  }
}
.logo {
  display: inline-block;
}

.logo--header:hover path {
  fill: #d22856;
}

.logo--header:active path {
  fill: #ffffff;
  opacity: 0.3;
}

.logo--header:hover .logo__image-header-mobile,
.logo--header:hover .logo__image-header-tablet {
  fill: #d22856;
}
.logo--header:active .logo__image-header-mobile,
.logo--header:active .logo__image-header-tablet {
  fill: #ffffff;
  opacity: 0.3;
}

.logo__image-header-mobile,
.logo__image-header-tablet {
  fill: #ffffff;
  transition: all 0.3s ease;
}

.logo__image-header-tablet {
  display: none;
}

.logo__image-header-desctop {
  display: none;
}
.logo__image-header-desctop path {
  transition: all 0.3s ease;
}

.logo--footer {
  margin-bottom: 41px;
}
.logo--footer:hover path {
  fill: #d22856;
}
.logo--footer:active path {
  opacity: 0.3;
}
.logo--footer:active .logo__image-footer-mobile--words {
  fill: #283645;
}
.logo--footer:active .logo__image-footer-desctop--words,
.logo--footer:active .logo__image-footer-desctop--stars {
  fill: #283645;
}

.logo__image-footer-mobile path,
.logo__image-footer-desctop path {
  transition: all 0.3s ease;
}

.logo__image-footer-desctop {
  display: none;
}

@media (min-width: 660px) {
  .logo__image-header-mobile {
    display: none;
  }

  .logo__image-header-tablet {
    display: block;
  }

  .logo--footer {
    margin-bottom: 0;
    margin-top: -27px;
    margin-right: -30px;
  }
}
@media (min-width: 1200px) {
  .logo--header {
    margin-top: -16px;
  }

  .logo__image-header-tablet {
    display: none;
  }

  .logo--footer {
    margin-right: 6px;
  }

  .logo__image-footer-mobile {
    display: none;
  }

  .logo__image-header-desctop {
    display: block;
  }

  .logo__image-footer-desctop {
    display: block;
  }
}
.menu-burger {
  position: relative;
  display: none;
  width: 50px;
  height: 24px;
  padding: 0;
  border: none;
  background-color: transparent;
}
.menu-burger.menu-burger--menu-close {
  display: block;
}
.menu-burger:hover::before {
  background-color: #d22856;
}
.menu-burger:hover::after {
  background-color: #d22856;
}
.menu-burger:hover .menu-burger__bar {
  background-color: #d22856;
}
.menu-burger:active::before {
  background-color: rgba(210, 40, 86, 0.3);
}
.menu-burger:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.menu-burger:active .menu-burger__bar {
  background-color: rgba(210, 40, 86, 0.3);
}
.menu-burger::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
  content: "";
  transition: all 0.3s ease;
}
.menu-burger::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
  content: "";
  transition: all 0.3s ease;
}
.menu-burger.menu-burger--closed:hover::before {
  background-color: #d22856;
}
.menu-burger.menu-burger--closed:hover::after {
  background-color: #d22856;
}
.menu-burger.menu-burger--closed:active::before {
  background-color: rgba(210, 40, 86, 0.3);
}
.menu-burger.menu-burger--closed:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.menu-burger.menu-burger--closed .menu-burger__bar {
  display: none;
}
.menu-burger.menu-burger--closed::before {
  top: 71%;
  left: 24px;
  width: 30px;
  transform: translateY(-50%) rotate(45deg);
  transition: all 0.3s ease;
}
.menu-burger.menu-burger--closed::after {
  top: 71%;
  left: 24px;
  width: 30px;
  transform: translateY(-50%) rotate(-45deg);
  transition: all 0.3s ease;
}

.menu-burger__bar {
  display: block;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
  transition: all 0.3s ease;
}

@media (min-width: 1200px) {
  .menu-burger.menu-burger--menu-close {
    display: none;
  }
}
