.upload__button--submit:hover {
  color: #ffffff;
  background-color: #d22856;
}
.upload__button--submit:active {
  color: rgba(255, 255, 255, 0.3);
}
.upload__button--cancel {
  color: rgba(40, 54, 69, 0.1);
  border: 3px solid rgba(40, 54, 69, 0.1);
}
.upload__button--cancel:hover {
  color: rgba(40, 54, 69, 0.3);
  border-color: rgba(40, 54, 69, 0.3);
}
.upload__button--cancel:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(40, 54, 69, 0.3);
  border-color: transparent;
}

@media (min-width: 660px) {
  .upload {
    margin-bottom: 5px;
  }

  .upload__descr {
    max-width: 422px;
    margin: 0 auto;
    margin-bottom: 71px;
    font-size: 18px;
  }

  .upload__content-box {
    padding: 57px 0 54px;
  }

  .upload__inner {
    display: flex;
    justify-content: center;
    padding: 0 20px;
  }

  .upload__container {
    padding: 0;
  }

  .upload__img-box {
    margin-bottom: 0;
    margin-right: 45px;
  }

  .upload__form {
    width: 100%;
    max-width: 275px;
    padding-top: 18px;
  }

  .upload__img {
    width: 300px;
    height: 400px;
  }

  .upload__list-box {
    padding-bottom: 0;
    margin-bottom: 65px;
    border-bottom: none;
  }

  .upload__range-input-box {
    position: relative;
    padding-bottom: 34px;
    margin-bottom: 36px;
    border-bottom: 1px solid rgba(40, 54, 69, 0.1);
  }
  .upload__range-input-box--hidden {
    display: block;
  }
  .upload__range-input-box::before {
    position: absolute;
    left: 5px;
    top: -9px;
    content: "";
  }
  .upload__range-input-box--crop::before {
    width: 22px;
    height: 22px;
    background: url("../img/crop-tablet.svg") no-repeat;
  }
  .upload__range-input-box--fill::before {
    width: 24px;
    height: 22px;
    background: url("../img/fill-tablet.svg") no-repeat;
  }
  .upload__range-input-box--contrast {
    margin-bottom: 0;
  }
  .upload__range-input-box--contrast::before {
    width: 16px;
    height: 16px;
    background: url("../img/contrast-tablet.svg") no-repeat;
  }

  .upload__range-input {
    max-width: 202px;
    margin: 0;
    margin-left: 51px;
  }
  .upload__range-input::-webkit-slider-runnable-track {
    max-width: 202px;
  }
  .upload__range-input::-moz-range-track {
    max-width: 202px;
  }

  .upload__item-button {
    display: none;
  }

  .upload__wrapper {
    margin-right: 10px;
  }

  .upload__button--submit {
    margin-bottom: 24px;
  }
}
@media (min-width: 1200px) {
  .upload {
    margin-bottom: 34px;
  }

  .upload__descr {
    margin-bottom: 65px;
  }

  .upload__content-box {
    padding: initial;
    background-color: initial;
  }

  .upload__inner {
    max-width: 940px;
    margin: 0 auto;
    padding: 57px 0 57px;
    background-color: #f2f2f2;
  }

  .upload__img-box {
    margin-left: 80px;
    margin-right: 85px;
  }

  .upload__form {
    max-width: 410px;
    margin-right: 60px;
  }

  .upload__list-box {
    margin-bottom: 179px;
  }

  .upload__range-input-box::before {
    top: -19px;
  }
  .upload__range-input-box--crop::before {
    width: 44px;
    height: 44px;
    background: url("../img/crop.svg") no-repeat;
  }
  .upload__range-input-box--fill::before {
    width: 48px;
    height: 43px;
    background: url("../img/fill.svg") no-repeat;
  }
  .upload__range-input-box--contrast {
    padding-bottom: 0;
    border-bottom: none;
  }
  .upload__range-input-box--contrast::before {
    width: 32px;
    height: 32px;
    background: url("../img/contrast.svg") no-repeat;
  }

  .upload__range-input {
    max-width: 288px;
    margin-left: 92px;
  }
  .upload__range-input::-webkit-slider-runnable-track {
    max-width: 288px;
  }
  .upload__range-input::-moz-range-track {
    max-width: 288px;
  }

  .upload__wrapper {
    flex-direction: initial;
    align-items: initial;
    justify-content: space-between;
    margin-right: 0;
  }

  .upload__button {
    max-width: 183px;
  }
  .upload__button--submit {
    margin-bottom: 0;
  }
}