*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;

  font-family: $opensans;
  font-size: 18px;
  line-height: 30px;
  color: $clay;
}

img {
  max-width: 100%;
  height: auto;

  vertical-align: middle;
}

svg {
  display: block;
}

button {
  cursor: pointer;
}
